*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
@media (max-width: 700px) {
  *{
    margin-left: .1rem;
    margin-right: .1rem;
    margin-top: o;
    padding-top: 0;
  }
  .mobile-navbar-dropdown{
    display: flex;
    justify-content: center;
    align-items: right;
    background-color:white;
  }
  .mobile-navbar-link{
    font-size: 2rem;
    font-family: serif;
  }
  .mobile-navbar-link2{
    color:black;
  }
  .mobile-navbar-link2:hover{
    background-color: #696969;
    color: whitesmoke;
  }
   .image-1{
     height: 420px !important;
   }
  .navbar1{
    display: none;
  }
  .logo_section{
    display: none;
  }
  .topbar_1{
    display: none;
  }
  .dekstop{
    display: none;
  }
  .topbar0{
    display: none;
  }
  .mobile_btn {
    background-color:red; 
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 1rem;
    padding: 0 1rem;
  }
  .mobile_btn:hover{
    background-color: black;
    color:#ec7834;
    transition: .5s;
  }
  
  .mobile_logo{
    display: flex;
    width:6rem;
    height:6rem;
  }
  .navbar_icon1{
    width: 3rem;
    height:3rem;
  }
  .navbar_icon1:hover{
    transform: rotate(360deg);
    transition: .2s;
  
  }
  .Donation1{
    flex-direction: column;
    margin-top: 1rem;
  }
  .main-slider{
    display: none;
  }
  .logo1{
    display: none;
  }
  .contact1{
    flex-direction: column;
  }
  .contact2{
    margin-top: 3rem !important;
  }
  
}
/*.....................................................................*/
.scale{
  transform: scale(0.9) translateX(90%);
  -webkit-transform: scale(0.9) translateX(90%);
  border-radius: 3px;
  box-shadow: 0 3px 10px 0 var(--secondary);
}
/*...................................dekstop responsive...................*/
@media (min-width:700px){
  .mobile_btn{
    display: none;
  }
  .mobile_navbar{
    display: none;
  }
  .mobilenavbar{
    display: none;
  }
  .mobile_logo{
    display: none;
  }
  .mobile-main-slider{
    display: none;
  }
}
/*..........*/
@media(max-width:1100px){
  .topbar0{
    display: none;
  }
  .topbar_1{
    display: none;
  }
  .container1 a{
    text-decoration: none;
    color: #000;
    font-size: 0.1rem;
    padding: 1rem;
    font-family: serif ;
  }
}
/*....................................navbar........................*/


.topbar0{
  background-color: black;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.topbar_1{
  padding-left: 2rem;
}
.bar{
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
}
.logo{
  width:8rem;
  height: 7rem;
}
.logo1{
  
  width: 10rem;
  height: 10rem;
  opacity: 40%
}
.btn {
  background-color: red; 
  border: none;
  color: white !important;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-top: 1rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  padding: 1rem 1rem !important;
}
.btn:hover{
  background-color: black;
  color:yellow;
  transition: .5s;
}
.navbar1{
  background-color: whitesmoke;
  margin-top: 0.5rem;
  
}
.container1{
  display: flex;
  justify-content: center;
}
.container1 a{
  text-decoration: none;
  color: #000;
  font-size: 1.3rem;
  padding: 1rem;
  font-family: serif ;
}
.container1 a.active{
  color: white;
  background-color:yellow;
}
.container1 a:hover{
  color: white;
  background-color:green;
  transition: 0.3s;
}


/* ....................... navbar end ...........................*/
.image-1{
  height: 550px;
  width: 100vw;
}
/* ....................... home-1 end ...........................*/
.activity_heading{
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  font-size: 2rem;
  margin-bottom: -1.9rem;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  background-color: whitesmoke;
  color: navy;
}
.activity_slider{
  margin-top: 2rem;
}
.slider2{
  width: 21.6rem !important;
  height: 15rem;
}
/* ....................... home-2 end ...........................*/
.about_section{
display: flex;
flex-wrap: nowrap;
margin-top: 2rem;
}
.about_heading{
  font-size: 3rem;
  color: navy;
}
.home_ending{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
  color: black;
  margin-top: 2rem;
  padding-bottom: 2rem;
}
.philosophy_heading{
  font-size: 3rem;
  font-style: oblique;
  font-family: bold;
  margin-top: 2rem;
  color: red;
}
.philosophy_content{
  font-size: 1.5rem;
  font-style: oblique;
  margin-top: .5rem;
}
.philosophy_btn{
  background-color: green;
  color: aliceblue;
  margin-top: 1rem;
  border-radius: 1rem;
  padding: 1rem 3rem;
}
.philosophy_btn:hover{
  background-color: navy;
  color: white;
}
.tribute_heading{
  font-size: 2rem;
  background-color: navy;
  color: white;
}
/*............................bottom..............*/
.bottom{
  margin-top:2rem;
  background-color: black;
  color: whitesmoke;
}
.bottom_image{
  width:6rem;
  height:5.5rem;
}
.bottom_news{
  display: flex;
  justify-content: center;
  align-items: center;
}
.news_bottom_headline{
  padding: 1rem;
  font-size: 1rem;
  font-family: bold;
}
.bottom_middle{
  margin: 0;
}
.bottom_pages{
  display: flex;
  flex-direction: column;
  justify-content:center;
  font-size: 1rem;
  
}
.bottom_news_headline{
  font-size: 2rem;
  font-family: bold;

}
.bottom_page_headline{
  font-size: 2rem;
  font-family: bold;
}
.quick_menu{
  display: flex;
  align-items: center;
  flex-direction: column; 
}
.quick_menu:hover{
  color: #ec7834;
  transition: .2s;
}
.bottom_rule{
  background-color: whitesmoke;
  margin-top: -.1rem;
  height:.1rem;
}
.bottom_rule:hover{
  background-color: #ec7834;
  transition: .2s;
}
/*..............................Refund..............*/
.refund_heading{
  background-color: black;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
  font-size: 2rem;
  font-family: serif;
  }
.refund_content{
  font-size: 1.2rem;
  margin-top: 1rem;
  font-family: serif;
}

/*..............................contact us......................*/

.contact1{
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 2rem;
}
.contact2{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.contact3{
  margin-top: 4rem;
}
.contact4{
  background-color: whitesmoke;
  padding: 3rem;
}
.Donation1{
  display: flex;
  justify-content: space-around;
  margin-top: 5rem;
  
}
.Donation_image{
  height: 28rem;
  width: 22rem;
  border-radius: 10px;
}
.Donation_content{
  margin: 2rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.donation_heading{
  font-size: 2rem;
  color: navy;
}
.about-us{
  height:28rem;
  width:20rem;
  border-radius: 1rem;

}
.text {
  display: block;
  width: 100px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
